.app.dark {
    background-color: #222;
    color: rgb(156, 156, 156);

    .chartGrid {
        stroke: rgba(228, 228, 228, 0.219);
    }

    .table {
        background-color: #111;

        .tableCell {
            color: rgb(156, 156, 156);
        }
    }

    .navbar {
        color: rgb(156, 156, 156);
        border-color: #333;

        .search {
            border-color: #999;
        }
    }

    .sidebar {
        background-color: #111;
        border-color: #333;

        .top {
            .logo {
                color: #999;
            }
        }
        hr {
            border-color: #333;
        }

        ul {
            li {
                &:hover {
                    background-color: #333;
                }
                .icon {
                    color: #999;
                }
            }
        }
    }

    .datatable {
        .datagrid {
            color: #999;
            border: none;

            .viewButton,
            .deleteButton,
            .cellWithStatus {
                color: #999;
                border: none;
            }
        }
    }

    input {
        background-color: transparent;
    }
}